	module.exports = {"data":{"markdownRemark":{"html":"<p>自分の今まで持ってきたノートPCでは初の主力機。このPCのおかげでノートPCの機動性を覚えデスクPCに戻れなくなった。 CPU能力の割に3D能力がイマイチ。いや、B5クラスノートにしちゃ頑張ってるほうなのかな。  特筆すべきはその頑丈さ。Windows Media Player再生したままカバンに入れて単車で走っても壊れない。(流石にそれを数か月間やってたらHDDが飛びましたが) 1年強の間お世話になった後に、やはり3D能力が気になり、<a href=\"/dynabook-sslx190\">TOSHIBA dynabookSS LX</a>に座を譲り売却。</p>\n<h2>当時はまったPCゲーム</h2>\n<ul>\n<li>\n<p>東方モノ全般</p>\n<h2>製作物・他やってたこと</h2>\n</li>\n<li>\n<p><a href=\"/!/thA/\">東方時封城</a>以前に当サイトにうｐした東方モノ全部</p>\n<h2>性能</h2>\n</li>\n<li>\n<p>CPU:intel Pentium M(Banias/1.1GHz)</p>\n</li>\n<li>\n<p>RAM:768MB</p>\n</li>\n<li>\n<p>VGA:intel 855GM(VRAM64MB)</p>\n</li>\n<li>\n<p>HDD:80GB</p>\n</li>\n<li>\n<p>CDD:CD-RW/DVD</p>\n</li>\n<li>\n<p>FDD:(NONE/代わりにSDカードスロットが)</p>\n</li>\n<li>\n<p>NET:100Base-TX/IEEE802.11b&#x26;g/56kbpsFaxModem</p>\n</li>\n<li>\n<p>SOUND:AC97?</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2004-06-01T20:00:00+09:00","strDate":"2004/6/1","title":"(2004-2005) Panasonic Let'sNote CF-W2D"}}},"pathContext":{"path":"/ja/blog/2004/06/01-cf-w2d/","langKey":"ja"}}

